import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplateMComponent } from '@modules/main-app/components/templates/template-m/template-m.component';
@Component({
  selector: 'app-feesv2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class Feesv2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Fees / Cost`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'Fees / Cost Page Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/our-fees/Fees_lg.jpg',
              title: 'OUR FEES',
              description:
                'It is important to us that we are transparent on all fees and costs associated with the use of the Partbnb Platform. Feel free to contact our team with any additional questions.',
              button: {}
            }
          },
          {
            keyTerm: 'Fees Page, Platform Fees',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/Box-bg-2-col-copy.png',
              title: 'Platform Fees',
              numRows: 1,
              boxes: [
                {
                  title: 'Buying / Selling of any Partbnb shares',
                  value: '1.75%',
                  description:
                    'If you are buying or selling parts on the platform, there will be a 1.75% transaction fee added to your order. For example, if you are buying 10 parts at $33.00 then your total order is $330, and your transaction fee will be $5,78 ($330 * 1.75%). The total charged will be $335.78.'
                }
              ]
            }
          },
          {
            keyTerm: 'Fees Page, Other Cost/Fees to Consider',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/Box-bg-2-col-copy.png',
              title: 'Other Costs / Fees to Consider',
              numRows: 2,
              boxes: [
                {
                  image: 'assets/icons/keylobby-icon.svg',
                  title: 'Keylobby Property Development / Management',
                  description:
                    'The company that manages the property(KeyLobby) charges a 15% fee(on gross revenue) to manage the property. This cost is built into the financial projections / statements of all listed properties. <br/> <br/> During the development phase of the property, KeyLobby will charge a development fee to perform the aquisition of the property, renovation work and set up to list the property as a short term rental.'
                },
                {
                  image: 'assets/icons/ach-icon.svg',
                  title: 'ACH / Wire Transfer Fees',
                  description:
                    'If you choose an ACH transfer there is a $5 transfer fee to send / receive funds (or 5% if under $100). <br/> <br/> Some banks will charge you a fee to initiate a Wire Transfer to us (please check with your provider). <br/><br/> If you have any questions in relations to fees or costs, please contact our team using Live Chat, or email us - team@partbnb.com.'
                }
              ]
            }
          }
        ];

      case TENANTS.TPC:
        return [
          {
            keyTerm: 'Fees / Cost Page Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/img/tpc/fees/Fees_lg.jpg',
              title: 'Our Fees',
              description:
                'It is important to us that we are transparent on all fees and costs associated with the use of the The Property Crowd Platform. Feel free to contact our team with any additional questions.',
              button: {}
            }
          },
          {
            keyTerm: 'Fees Page, Platform Fees',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/img/tpc/fees/box.jpg',
              title: 'Platform Fees',
              numRows: 2,
              boxes: [
                {
                  title: 'Buying PropertyShares',
                  value: '4.00%',
                  description:
                    'If you are buying PropertyShares on the platform, there will be a 4% transaction fee payable.  The amount will be shown as part of the total dollar amount you have opted to invest.'
                },
                {
                  title: 'Selling PropertyShares                    ',
                  value: '0.00%',
                  description:
                    'When you sell your PropertyShares there is no fee payable. You get to keep all the money from the ale of yoru PropertyShares.'
                }
              ]
            }
          },
          {
            keyTerm: 'Fees Page, Other Cost/Fees to Consider',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/img/tpc/fees/box.jpg',
              title: 'Other Costs / Fees to Consider',
              numRows: 3,
              boxes: [
                {
                  image: '/assets/img/tpc/fees/noun_management_939453.svg',
                  title: 'Property Management Fees',
                  description:
                    'The Property Management Company charges a standard management fee to look after the  property.  The standard rate is 7.5%.'
                },
                {
                  image: '/assets/img/tpc/fees/noun_management_1133711.svg',
                  title: "Director's Fee",
                  description:
                    'Each property is held by a Holding Company.  As a part owner you own shares in that company.  The independant director responsible for each company is paid $750 which is a fee paid by the holding company not by the investors.'
                },
                {
                  image: '/assets/img/tpc/fees/noun_service_1235555.svg',
                  title: 'Service Fee',
                  description:
                    "The Property Crowd charges a fee of $1000 to the holding company that owns the property. This fee is to host and manage a property on The Property Crowd's platform. This is not a fee charged to investors."
                }
              ]
            }
          }
        ];
      default:
        break;
    }
  }
}
