import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplateMComponent } from '@modules/main-app/components/templates/template-m/template-m.component';
import { TemplateRComponent } from '@modules/main-app/components/templates/template-r/template-r.component';
@Component({
  selector: 'app-our-purpose',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class OurPurposeComponent implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Our Purpose`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [];
      case TENANTS.TPC:
        return [
          {
            keyTerm: 'Our Purpose Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/img/tpc/our_purpose/Our_Vision_lg.jpg',
              title: 'Our Purpose',
              description:
                'Enabling New Zealanders to invest in quality property using a simple, transparent investment model. To use technology to benefit both investors and tenants. Enabling all New Zealanders to invest in quality properties that are professionally managed and provide security of tenure to tenants.',
              button: {}
            }
          },
          {
            keyTerm: 'Fees Page, Other Cost/Fees to Consider',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/img/tpc/our_purpose/box.jpg',
              title: 'Our Values',
              isCenter: true,
              numRows: 3,
              boxes: [
                {
                  image:
                    '/assets/img/tpc/our_purpose/noun_Home_Love_564813.svg',
                  title: 'Socially Responsible',
                  description:
                    'Our tenants enjoy the benefits of security with the option of longevity in long-term tenancies. We provide quality, well maintained comfortable houses our tenants can call home.'
                },
                {
                  image: '/assets/img/tpc/our_purpose/noun_Idea_2186638.svg',
                  title: 'Innovative',
                  description:
                    'A new take on property investment, giving all New Zealanders the opportunity to realise the Kiwi dream - investing in residential property.'
                },
                {
                  image:
                    '/assets/img/tpc/our_purpose/noun_House_Search_671222.svg',
                  title: 'Transparent',
                  description:
                    'We provide you with honest real data with all the information you need on the properties, your investments and our fees at your fingertips.'
                },
                {
                  image:
                    '/assets/img/tpc/our_purpose/noun_find_document_2039248.svg',
                  title: 'Quality',
                  description:
                    'Our properties are carefully selected and must pass our evaluation criteria which assesses build quality, compliance, location and scope for return.'
                },
                {
                  image:
                    '/assets/img/tpc/our_purpose/noun_Security_1879715.svg',
                  title: 'Security',
                  description:
                    'We place high importance on minimising preventable risks. Utilising data encryption to protect your information, asset segregation for your investments and we are big on compliance.'
                },
                {
                  image:
                    '/assets/img/tpc/our_purpose/noun_Order_Management_2100516.svg',
                  title: 'Compliance',
                  description:
                    'We pride ourselves in integrity with the highest ethical standards, we work closely with New Zealand Financial Markets Authority and are fully compliant with New Zealand crowdfunding law.'
                }
              ]
            }
          },
          {
            keyTerm: 'Management Team Section',
            component: TemplateRComponent,
            data: {
              title: 'Management Team',
              numRows: 3,
              boxes: [
                {
                  image: '/assets/img/tpc/our_purpose/Jim.png',
                  id: 'Jim',
                  name: 'Jim Janse',
                  position: 'Founder & CEO',
                  description: {
                    less:
                      'Jim Janse is a strategic entrepreneur with exceptional leadership and operational skills.',
                    more:
                      "Jim has excellent interpersonal skills, strong financial acumen and experience in regulation management and compliance. Jim's occupational skills include Veterinary Medicine and Photography, he has a history of more than four successful start-up businesses in various industries."
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/James.png',
                  id: 'James',
                  name: 'James Punnett',
                  position: 'Chief Product Officer',
                  description: {
                    less:
                      'As a serial entrepreneur James has been involved in numerous startup businesses including founding LifeDirect, the largest online insurance marketplace in New Zealand.',
                    more:
                      'James started in the insurance space, but has spent the last 3 years in the Real Estate / Lending sector delivering a new Fintech platform for the largest real estate businesses in Australia & New Zealand. In addition to this James has built a property portfolio of short term rental properties around New Zealand, and serves on various boards.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Jonathan.png',
                  id: 'Jonathan',
                  name: 'Jonathan Michell',
                  position: 'Founder',
                  description: {
                    less:
                      'Jonathan Michell is a successful business owner and registered financial advisor, with specialist mortgage and insurance skills and a comprehensive understanding of tax, legal structures and CRM systems.',
                    more:
                      ' Jonathan has extensive knowledge of the property market, with personal experience in property investment and development, managing renovations, subdivisions and a portfolio of rental properties.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Shaun.png',
                  id: 'Shaun',
                  name: 'Shaun Fuchs',
                  position: 'Founder & CTO',
                  description: {
                    less:
                      "Shaun Fuchs is a full-stack tech-lead and architect with over 18 years' experience in building systems for enterprises & start-ups.",
                    more:
                      'Shaun has specialist skills in online system design with experience building financial systems, security, analytics and reporting. Shaun has a fortitude for strategic development and a passion for new and innovative technology solutions.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Luke.png',
                  id: 'Luke',
                  name: 'Luke Snedden',
                  position: 'Founder & BDM',
                  description: {
                    less:
                      'Luke Snedden is a successful business operator with skills established from a solid foundation of business experience.',
                    more:
                      'Luke is proficient in developing and driving business strategies, developing and implementing marketing plans, utilising digital technology, implementing systems, allocating resources, hiring and managing staff and ensuring high quality customer service.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Kirsty.png',
                  id: 'Kirsty',
                  name: 'Kirsty McAlpine',
                  position: 'Compliance',
                  description: {
                    less:
                      'Kirsty draws on 25 years of experience in the finance and banking sector. In that time, shes worked for some of the major banks in the UK such as Deutsche Bank and ABN Amro.',
                    more:
                      'In the last 12 years, Kirsty has focused on the operational side with an emphasis on compliance. She has a diverse range of skills and a strong management background.'
                  }
                }
              ]
            }
          },
          {
            keyTerm: 'Advisory Board Section',
            component: TemplateRComponent,
            data: {
              title: 'Advisory Board',
              numRows: 3,
              boxes: [
                {
                  image: '/assets/img/tpc/our_purpose/Uddhav.png',
                  id: 'Uddhav',
                  name: 'Uddhav Kirtikar',
                  position: 'AML Advisor',
                  description: {
                    less:
                      'While at the FMA, Uddhav played an important part in establishing the AML/CFT regime.',
                    more:
                      'He was FMAs lead writer for most of the AML/CFT guidelines published jointly by the Financial Markets Authority, Department of Internal Affairs and the Reserve Bank of New Zealand. He also assisted in designing the templates and process that FMA used when it commenced its AML/CFT supervisory work. While at FMA, Uddhav played an important part in establishing the AML/CFT regime. He was FMAs lead writer for most of the AML/CFT guidelines published jointly by the Financial Markets Authority, Department of Internal Affairs and the Reserve Bank of New Zealand. He also assisted in designing the templates and process that FMA used when it commenced its AML/CFT supervisory work. '
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Patrick.png',
                  id: 'Patrick',
                  name: 'Patrick Goggin',
                  position: 'CRS Advisor',
                  description: {
                    less:
                      'Patrick is a highly experienced senior manager with executive experience as CFO governing business transformation, organisational performance, and has extensive professional experience leading regulatory compliance, and finance/corporate groups.',
                    more:
                      'He is a strategic and innovative compliance thinker and has led strategic projects enabling business transformation. He has governance experience on key IR boards: business performance, strategy, programme, technical governance, tax disputes, rulings, AEOI and intel-led. Patrick has extensive large business and audit experience, expertise in fraud and digital forensics, and fronting media and presenting on compliance.  He has international experience with Australian, UK, and Canadian tax authorities in several areas and from OECD groups in audit, large business/ capacity building.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Glenn.png',
                  id: 'Glenn',
                  name: 'Glenn Marvin',
                  position: 'Acting CMO',
                  description: {
                    less:
                      'Glenn has many years corporate experience with an emphasis on Marketing and building Digital businesses, with an open, honest and often unorthodox approach to utilising Social Media as a cornerstone strategy.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Chis.png',
                  id: 'Chis',
                  name: 'Chis Luoni FCIS',
                  position: 'Governance',
                  description: {
                    less:
                      'Chris Luoni is from Hamilton and was a partner in PricewaterhouseCoopers and predecessor firms for 24 years retiring in 2009.',
                    more:
                      'Chris is a company director of several private companies including world class hospitality businesses trading as Huka Retreats based in NZ, Fiji, Namiba and South Africa. Until recently, he was also the co-owner of The Omnia Group, a company which provides personality profiling assessments and in his spare time breeds thoroughbred horses on a small farm outside of Hamilton. Chris is involved in a number of national Not-for-Profit organisations. He is Chairman of the NZ Racing Hall of Fame and was, until recently, Chairman of the Duke of Edinburghs Hillary Award.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Alistair.png',
                  id: 'Alistair',
                  name: 'Alistair Helm',
                  position: 'Property Advisor',
                  description: {
                    less:
                      'Alistair Helm has a wealth of knowledge and insight into the dynamics of the property market and specialist knowledge of the Devonport market.',
                    more:
                      'With executive roles at both Realestate co nz and Trade Me he is also a respected authority on digital marketing and all aspects of property marketing.'
                  }
                },
                {
                  image: '/assets/img/tpc/our_purpose/Edward.png',
                  id: 'Edward',
                  name: 'Edward Bell',
                  position: 'Marketing',
                  description: {
                    less:
                      'Experienced FinTech Marketing Specialist & Member of the Chartered Institute or Marketing (MCIM)'
                  }
                }
              ]
            }
          }
        ];
      default:
        break;
    }
  }
}
