import { Pipe, PipeTransform } from '@angular/core';
import * as Fuse from 'fuse.js';

@Pipe({
  name: 'filterFaqs'
})
export class FilterFaqsPipe implements PipeTransform {
  transform(items, searchKeyword, keys): any[] {
    const options: object = {
      shouldSort: true,
      threshold: 0.6,
      location: 0,
      distance: 100,
      maxPatternLength: 32,
      minMatchCharLength: 1,
      keys: keys
    };

    const fuse: any = new Fuse(items, options);
    return searchKeyword ? fuse.search(searchKeyword.toLowerCase()) : [];
  }
}
