import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { HelpCenter } from './help-center.model';
import { helpCenterBuilderUI } from './help-center.mapper';

@Injectable({
  providedIn: 'root'
})
export class HelpCenterService {
  baseUrl = `https://test-tpc.partbnb.com`;

  constructor(private _http: HttpClient) {}

  getFAQs(): Observable<HelpCenter> {
    return this._http
      .get(`${this.baseUrl}/api/tpc/faqs`)
      .pipe(map(helpCenterBuilderUI));
  }
}
