import { Component, OnInit, Input } from '@angular/core';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { Router } from '@angular/router';

@Component({
  selector: 'template-k',
  templateUrl: './template-k.component.html',
  styleUrls: ['./template-k.component.scss']
})
export class TemplateKComponent implements OnInit {
  currentTenant;
  baseClassName;

  @Input() resources: any;

  constructor(
    private _viewportService: ViewportService,
    private _tenantCheckerService: TenantCheckerService,
    private router: Router
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.baseClassName = this._viewportService.sizeClassName();
    });
  }

  ngOnInit() {
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.baseClassName = this._viewportService.sizeClassName();
  }

  navigateUrl(ref) {
    if (ref.openNewTab) {
      window.open(ref.link, '_blank');
    } else {
      this.router.navigate([ref.link]);
    }
  }
}
