import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplateKComponent } from '@modules/main-app/components/templates/template-k/template-k.component';
import { TemplateOComponent } from '@modules/main-app/components/templates/template-o/template-o.component';
@Component({
  selector: 'app-why-investv2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class WhyInvestv2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Why Invest`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'Why Invest Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/img/why-invest/Invest_lg.jpg',
              title: 'WHY INVEST IN A PROPERTY ?',
              description:
                ' We think investing in Real Estate is a great way to diversify your portfolio. Find out more about why you should consider this asset class.'
            }
          },
          {
            keyTerm: 'Why Invest Content',
            component: TemplateKComponent,
            data: [
              {
                title: 'Real Estate is simple and easy to understand',
                content:
                  'If you read the financial statements of a company you own stock in or try to make head and tail of a profit/loss statement for a large corporation, you will understand why Real Estate is so much simpler. <br/><br/>It is straightforward to calculate income on a Vacation rental (Nightly Rate * Occupancy Rate * 365).<br/><br/>Once you have taken into account of expenses, you will know your Net income, and very quickly your NET yield on the investment. At Partbnb we have made it simple and transparent on all of these figures for every project.',
                image: 'assets/img/why-invest/Image1@2x.png'
              },
              {
                title: 'You own a physical Security',
                content:
                  "A property is something that you can visit. It is one of the few investments where you can physically review your investment, unlike stocks where you will likely never see any of the company's physical assets<br/><br/>Partbnb allows part owners to book and stay at a discounted rate in the property that they own. You can go and visit your property and even touch the walls if you wish!",
                image: 'assets/img/why-invest/Image2@2x.png'
              },
              {
                title: 'Stability in Income',
                content:
                  'Everyone needs a place to live, or in our case a place to stay when they are on vacation. While stock markets move up and down, there is always a need for accommodation.',
                image: 'assets/img/why-invest/Image3@2x.png'
              },
              {
                title: 'Long Term Appreciation',
                content:
                  "Over the long term, real estate has been shown to rise in price, providing capital growth (or gains) to those that own it. Partbnb ensures that investors can take advantage of these gains by selling their parts on the platform in the future and realizing the difference. <br/><br/>Of course, prices don't always increase, and there is still a risk that over some time the property value may decrease.",
                image: 'assets/img/why-invest/Image4@2x.png'
              }
            ]
          },
          {
            keyTerm: 'Why Invest Bottom Content ',
            component: TemplateOComponent,
            data: {
              backgroundURL: '/assets/img/why-invest/Bg_bot@2x.png',
              description:
                "Here's how much the median home value in the U.S. has changed between 1940 and 2000:",
              image: {
                desktop: 'assets/img/why-invest/bottom-chart.png',
                tablet: 'assets/img/why-invest/bottom-chart.png',
                mobile: 'assets/img/why-invest/bottom-chart-mobile.png'
              }
            }
          }
        ];
      case TENANTS.TPC:
        return [];
      default:
        break;
    }
  }
}
