import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateGComponent } from '@modules/main-app/components/templates/template-g/template-g.component';
import { TemplateMComponent } from '@modules/main-app/components/templates/template-m/template-m.component';
@Component({
  selector: 'app-sellingv2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class Sellingv2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Selling Parts`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'Selling Parts, Header',
            component: TemplateGComponent,
            data: {
              backgroundURL: '/assets/selling-exiting/Header@2x.png',
              title: 'HOW DO I SELL MY PARTS?',
              description:
                'Sell your parts to other approved investor in just a few easy steps.',
              image: 'assets/selling-exiting/snapshots@2x.png'
            }
          },
          {
            keyTerm: 'Selling Parts, Content',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/Box-bg-2-col-copy.png',
              numRows: 2,
              description:
                'Please Note - The option to sell a part will only become available after you have held the part for a minimum of 6 months. This is an SEC requirement.',
              boxes: [
                {
                  image: 'assets/icons/marketplace-icon.svg',
                  title: 'Choose a Price',
                  numberIcon: 'assets/icons/number1.svg',
                  description:
                    'First, find a comfortable price point. You can use the yearly property valuation to assist you with setting a price, or look at recent sale values of other property parts. '
                },
                {
                  image: 'assets/icons/investors-icon.svg',
                  title: 'List on the Platform',
                  numberIcon: 'assets/icons/number2.svg',
                  description:
                    'Once you have set a price, your parts will be listed on the platform for sale to other approved Accredited Investors. <br/> <br/> <small>Note - If other investors have also listed parts for sale in the same property, the lowest priced parts will be purchased by investors first.</small>'
                },
                {
                  image: 'assets/icons/receiving-icon.svg',
                  title: 'Parts Sold',
                  numberIcon: 'assets/icons/number3.svg',
                  description:
                    'If another investor does purchase one or more of your parts, you will receive an email notification. The proceeds of the sale will go directly into your digital wallet.<br/><br/><small>Note - the usual transaction fee of 1.75% will apply to the sale of any parts. <a href="/#/app/feesv2">Please visit the Fees / Costs </a> page for more information</small>'
                },
                {
                  image: 'assets/icons/bank-icon.svg',
                  title: 'Withdraw or Re-invest',
                  numberIcon: 'assets/icons/number4.svg',
                  description:
                    ' Once your funds are in your digital wallet, you now have the choice to re-invest them in another property, or withdraw the funds to your bank account. '
                }
              ]
            }
          }
        ];

      case TENANTS.TPC:
        return [
          {
            keyTerm: 'Selling Parts, Header',
            component: TemplateGComponent,
            data: {
              backgroundURL:
                '/assets/img/tpc/how_do_sell_my_property_shares/How_Do_I_Exit_lg.jpg',
              title: 'How do I sell my PropertyShares?',
              description:
                'Sell your propertyshares to other investors in just a few easy steps.',
              image: '/assets/img/tpc/how_do_sell_my_property_shares/Inside.png'
            }
          },
          {
            keyTerm: 'Selling Parts, Content',
            component: TemplateMComponent,
            data: {
              boxBackground:
                '/assets/img/tpc/how_do_sell_my_property_shares/box.jpg',
              numRows: 2,
              description:
                'Please Note - The option to sell a part will only become available after you have held the part for a minimum of 6 months. This is an SEC requirement.',
              boxes: [
                {
                  image:
                    '/assets/img/tpc/how_do_sell_my_property_shares/Marketplace-icon.svg',
                  title: 'List on the Marketplace',
                  numberIcon:
                    '/assets/img/tpc/how_do_sell_my_property_shares/1.svg',
                  description:
                    "Choose how many PropertyShares you'd like to sell and at what price point. You can use the property valuation to assist you with setting a price, or look at recent sale values of other PropertyShares that have sold."
                },
                {
                  image:
                    '/assets/img/tpc/how_do_sell_my_property_shares/noun_website_1636942.svg',
                  title: 'List on the Platform',
                  numberIcon:
                    '/assets/img/tpc/how_do_sell_my_property_shares/2.svg',
                  description:
                    'Once you have set a price, your PropertyShares will be listed on the platform for sale to other investors. <br/> <br/>Note - If other investors have also listed PropertyShares for sale in the same property, the lowest priced PropertyShatres will be purchased by investors first.'
                },
                {
                  image:
                    '/assets/img/tpc/how_do_sell_my_property_shares/receiving-icon.svg',
                  title: 'Parts Sold',
                  numberIcon:
                    '/assets/img/tpc/how_do_sell_my_property_shares/3.svg',
                  description:
                    'If another investor does purchase your PropertyShares, you will receive an email notification. The proceeds of the sale will go directly into your wallet.<br/> <br/>Note - there is no fee for selling PropertyShares. Please visit the Fees / Costs page for more information.'
                },
                {
                  image:
                    '/assets/img/tpc/how_do_sell_my_property_shares/bank-icon.svg',
                  title: 'Withdraw or Re-invest',
                  numberIcon:
                    '/assets/img/tpc/how_do_sell_my_property_shares/4.svg',
                  description:
                    'Once your funds are in your digital wallet, you now have the choice to re-invest them in another property, or withdraw the funds to your bank account.'
                }
              ]
            }
          }
        ];
      default:
        break;
    }
  }
}
