import { Component, OnInit } from '@angular/core';
import faq from 'assets/data/faq.json';
import groupBy from 'app/shared/utilities/groupBy';
import { Title } from '@angular/platform-browser';

import { FilterFaq } from './pipes/FilterFaq.pipe';
@Component({
  selector: 'app-help-center',
  templateUrl: './help-center.component.html',
  styleUrls: ['./help-center.component.scss'],
  providers: [FilterFaq]
})
export class HelpCenterComponent implements OnInit {
  contentData: object = groupBy(faq, 'Category');
  categoryKeys: string[] = Object.keys(this.contentData);
  searchInput: string;
  faqArray: Object[] = faq;
  constructor(private filterFaq: FilterFaq, private title: Title) {}
  search(keyword) {
    const rawResult = this.filterFaq.transform(faq, keyword);
    this.contentData = groupBy(rawResult, 'Category');
    this.searchInput = null;
  }

  ngOnInit() {
    this.title.setTitle(`Partbnb - Help Center`);
  }
}
