import { Component, OnInit, AfterViewInit } from '@angular/core';
import { NgxPlaidLinkService } from 'ngx-plaid-link';
import { PlaidLinkHandler } from 'ngx-plaid-link/lib/ngx-plaid-link-handler';
import { PlaidConfig } from '@shared/services/plaid/interfaces';
import { PLAID } from '@env/environment';
import { BsModalService } from 'ngx-bootstrap';
import { PaymentMethodComponent } from '@shared/components/plaid';
import { ModalService } from '@shared/services/modal/modal.service';
@Component({
  selector: 'app-purchase',
  templateUrl: './purchase.component.html',
  styleUrls: ['./purchase.component.scss']
})
export class PurchaseComponent implements OnInit, AfterViewInit {
  private plaidLinkHandler: PlaidLinkHandler;
  username;

  private config: PlaidConfig = {
    clientName: 'Partbnb',
    apiVersion: 'v2',
    // env: 'sandbox',
    env: 'development',
    institution: null,
    token: null,
    webhook: PLAID.WEBHOOK,
    // product: ['transactions', 'auth', 'income', 'identity', 'assets'],
    product: ['transactions', 'auth'],
    key: PLAID.PUBLIC_KEY
  };

  constructor(
    private plaidLinkService: NgxPlaidLinkService,
    private bsModalService: BsModalService,
    private _modalService: ModalService
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {
    this.plaidLinkService
      .createPlaid(
        Object.assign({}, this.config, {
          onSuccess: (token, metadata) => this.onSuccess(token, metadata),
          onExit: (error, metadata) => this.onExit(error, metadata),
          onEvent: (eventName, metadata) => this.onEvent(eventName, metadata)
        })
      )
      .then((handler: PlaidLinkHandler) => {
        this.plaidLinkHandler = handler;
      });
  }

  open() {
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    if (currentUser && currentUser.isLogin) {
      this.username = currentUser.username;
      this.plaidLinkHandler.open();
    }
  }

  exit() {
    this.plaidLinkHandler.exit();
  }

  onSuccess(token, metadata) {
    const postData = {
      public_token: token,
      data: metadata,
      username: this.username
    };
    this.openModal(postData);
  }

  onEvent(eventName, metadata) {
    // console.log('We got an event:', eventName);
    // console.log('We got metadata:', metadata);
  }

  onExit(error, metadata) {
    // console.log('We exited:', error);
    // console.log('We got metadata:', metadata);
  }

  openModal(data: any) {
    const initialState = {
      plaidData: data
    };
    this._modalService.modalInstance(PaymentMethodComponent, {
      initialState,
      animated: false,
      keyboard: false,
      ignoreBackdropClick: true,
      class: 'plaid-modal'
    });
    // (<ConfirmationComponent>modalRef.content).onClose.subscribe(result => {
    //   if (result === true) {
    //     this._utility.deleteArea(item.id).subscribe(res => {
    //       if (res) {
    //         this.getAreas();
    //       }
    //     });
    //   }
    // });
  }
}
