import {
  Component,
  OnInit,
  ChangeDetectorRef,
  HostListener,
  Input
} from '@angular/core';
import { TENANTS } from '@shared/constants/tenants';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { VgAPI } from 'videogular2/core';

@Component({
  selector: 'template-j',
  templateUrl: './template-j.component.html',
  styleUrls: ['./template-j.component.scss']
})
export class TemplateJComponent implements OnInit {
  currentTenant;
  baseClassName;
  vgApi: VgAPI;
  videoHeight;
  videoWidth;
  minVideoHeight;
  maxVideoHeight;
  currentFrontContent;
  showPause;
  isDesktop;
  isTablet;
  isMobile;
  defaultVideoHeights = {
    desktop: { min: '420', max: '580' },
    tablet: { min: '370', max: '450' },
    mobile: { min: '185', max: '475' }
  };

  @Input() resources: any;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this.calculateVideoHeight();
      this.changeDetectorRef.detectChanges();
    }, 500);
  }

  constructor(
    private _viewportService: ViewportService,
    private _tenantCheckerService: TenantCheckerService,
    private changeDetectorRef: ChangeDetectorRef
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.baseClassName = this._viewportService.sizeClassName();
      this.isDesktop = this._viewportService.isDesktop();
      this.isTablet = this._viewportService.isTablet();
      this.isMobile = this._viewportService.isMobile();
      this.changeDetectorRef.detectChanges();
    });
  }

  ngOnInit() {
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.baseClassName = this._viewportService.sizeClassName();
    this.isDesktop = this._viewportService.isDesktop();
    this.isTablet = this._viewportService.isTablet();
    this.isMobile = this._viewportService.isMobile();
    this.calculateVideoHeight();
    this.currentFrontContent = this.getFrontContent(
      this.resources.fronts,
      'BEFORE'
    );
    this.showPause = false;
  }

  onPlayerReady(vgApi: VgAPI) {
    this.vgApi = vgApi;

    this.vgApi.getDefaultMedia().subscriptions.ended.subscribe(() => {
      this.vgApi.getDefaultMedia().currentTime = 0;
      this.videoHeight = this.minVideoHeight;
      this.currentFrontContent = this.getFrontContent(
        this.resources.fronts,
        'AFTER'
      );
    });
  }

  videoEvent(type) {
    if (type === 'BEFORE' || type === 'AFTER') {
      this.videoHeight = this.maxVideoHeight;
      this.vgApi.play();
      this.currentFrontContent = this.getFrontContent(
        this.resources.fronts,
        'DURING'
      );
    } else if (type === 'DURING') {
      this.videoHeight = this.minVideoHeight;
      this.vgApi.pause();
      this.currentFrontContent = this.getFrontContent(
        this.resources.fronts,
        'BEFORE'
      );
    }
  }

  getFrontContent(fronts, type) {
    return fronts.filter(front => {
      if (front.type === type) {
        return front;
      }
    })[0];
  }

  onShowPause() {
    setTimeout(() => {
      if (this.currentFrontContent.type === 'DURING') {
        this.showPause = true;
      }
    }, 500);
  }

  onHidePause() {
    setTimeout(() => {
      if (this.currentFrontContent.type === 'DURING') {
        this.showPause = false;
      }
    }, 500);
  }

  calculateVideoHeight() {
    this.videoWidth = window.innerWidth;
    this.minVideoHeight = Math.round(this.videoWidth * 0.35);
    this.maxVideoHeight = Math.round(this.videoWidth * 0.48);

    if (
      this.isDesktop &&
      this.minVideoHeight < this.defaultVideoHeights.desktop.min
    ) {
      this.minVideoHeight = this.defaultVideoHeights.desktop.min;
      this.maxVideoHeight = this.defaultVideoHeights.desktop.max;
    } else if (
      this.isTablet &&
      this.minVideoHeight < this.defaultVideoHeights.tablet.min
    ) {
      this.minVideoHeight = this.defaultVideoHeights.tablet.min;
      this.maxVideoHeight = this.defaultVideoHeights.tablet.max;
    } else if (
      this.isMobile &&
      this.minVideoHeight < this.defaultVideoHeights.mobile.min
    ) {
      this.minVideoHeight = this.defaultVideoHeights.mobile.max;
      this.maxVideoHeight = this.defaultVideoHeights.mobile.min;
    }

    this.videoHeight = this.minVideoHeight;
  }
}
