import { Component, OnInit, Input } from '@angular/core';
import {
  FloorPlanLayout,
  Timeline,
  Video
} from '@shared/api/property-details/property-details.model';
import { PhotoViewerService } from '@shared/services/photo-viewer/photo-viewer.service';
import { environment } from '@env/environment';
import { VideoPlayerService } from '@shared/services';

@Component({
  selector: 'tab-property-details',
  templateUrl: './tab-property-details.component.html',
  styleUrls: ['./tab-property-details.component.scss']
})
export class TabPropertyDetailsComponent implements OnInit {
  floorPlanLayout: FloorPlanLayout;
  timelines: Timeline[];
  video: Video;
  videoHeight = 200;
  isWalkThroughShow: boolean = true;
  images: string = '/assets/img/walkthrough.jpg';

  @Input()
  set FloorPlanLayout(layout) {
    this.floorPlanLayout = layout;
  }

  @Input()
  set Timelines(timelines) {
    this.timelines = timelines;
  }

  @Input()
  set Video(video) {
    this.video = video;
    this.isWalkThroughShow = !!(video && video.videoUrl);
  }

  @Input()
  set Images(images: Array<any>) {
    if (images && images.length) {
      this.images = `${environment.baseUrl}${images[0].filePath}`;
    }
  }

  constructor(
    private _photoViewerService: PhotoViewerService,
    private _videoPlayer: VideoPlayerService
  ) {}

  ngOnInit() {}

  showImageViewer(filePath, status: number) {
    if (status === 3) {
      return;
    }
    this._photoViewerService.toggle();
    this._photoViewerService.setImage(filePath);
  }

  playVideo(videoLink, title) {
    this._videoPlayer.toggle();
    this._videoPlayer.setVideo(videoLink);
    this._videoPlayer.setTitle(title);
  }
}
