import {
  Component,
  OnInit,
  HostListener,
  AfterViewInit,
  Inject,
  Input
} from '@angular/core';
import { TENANTS } from '@shared/constants/tenants';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { interval } from 'rxjs';
import { ScrollLockService } from '@shared/services/scroll-lock/scroll-lock.service';

@Component({
  selector: 'template-i',
  templateUrl: './template-i.component.html',
  styleUrls: ['./template-i.component.scss']
})
export class TemplateIComponent implements OnInit, AfterViewInit {
  currentTenant;
  baseClassName;
  testimonyCurrentIndex = 0;
  testimonyInterval = interval(10000);
  testimonialPagePosition = 0;
  onPosition = false;

  @Input() resources: any;

  constructor(
    private _viewportService: ViewportService,
    private _tenantCheckerService: TenantCheckerService,
    private _scrollLockService: ScrollLockService
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.baseClassName = this._viewportService.sizeClassName();
    });

    this.testimonyInterval.subscribe(() => {
      this.updateTestimony();
    });

    this._scrollLockService.updatingCurrentViewPosition.subscribe(
      scrollPosition => {
        setTimeout(() => {
          this.onPosition = scrollPosition >= this.testimonialPagePosition;
        }, 500);
      }
    );
  }

  ngOnInit() {
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.baseClassName = this._viewportService.sizeClassName();
  }

  ngAfterViewInit() {
    this.testimonialPagePosition =
      document.getElementById('testimonials').getBoundingClientRect().top - 65;
  }

  updateTestimony() {
    this.testimonyCurrentIndex =
      this.testimonyCurrentIndex < this.resources.testimonials.length - 1
        ? this.testimonyCurrentIndex + 1
        : 0;
  }
}
