import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Input
} from '@angular/core';
import { TENANTS } from '@shared/constants/tenants';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { HomeVideoService } from '@shared/services/home-video-service/home-video.service';
import { EmbedVideoService } from 'ngx-embed-video';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';

@Component({
  selector: 'template-b',
  templateUrl: './template-b.component.html',
  styleUrls: ['./template-b.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TemplateBComponent implements OnInit {
  currentTenant;
  baseClassName;
  video;
  isDesktop;
  isTablet;
  isMobile;

  @Input() resources: any;

  constructor(
    private _viewportService: ViewportService,
    private _homeVideoService: HomeVideoService,
    private embedVideoService: EmbedVideoService,
    private changeDetectorRef: ChangeDetectorRef,
    private _tenantCheckerService: TenantCheckerService
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.baseClassName = this._viewportService.sizeClassName();
      this.isDesktop = this._viewportService.isDesktop();
      this.isTablet = this._viewportService.isTablet();
      this.isMobile = this._viewportService.isMobile();
      this.changeDetectorRef.detectChanges();
    });
  }

  ngOnInit() {
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.baseClassName = this._viewportService.sizeClassName();
    this.isDesktop = this._viewportService.isDesktop();
    this.isTablet = this._viewportService.isTablet();
    this.isMobile = this._viewportService.isMobile();
    // this.changeDetectorRef.detectChanges();
  }

  showVideo() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        this._homeVideoService.toggle();
        break;

      default:
        break;
    }
  }

  embedVideo(id) {
    let height;
    if (this.isDesktop) {
      height = '425px';
    } else if (this.isTablet) {
      height = '405px';
    } else {
      height = '205px';
    }
    return this.embedVideoService.embed_vimeo(id, {
      attr: { width: '100%', height: height },
      query: { color: 'fff' }
    });
  }
}
