import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EMAIL } from '@shared/constants/email.ts';
import { FormEmailService } from '@modules/main-app/services/form-email/form-email.service';
import { ConvertKitService } from '@shared/services';

@Component({
  selector: 'app-corner-stone-club',
  templateUrl: './corner-stone-club.component.html',
  styleUrls: ['./corner-stone-club.component.scss']
})
export class CornerStoneClubComponent implements OnInit {
  cornerStoneClubForm: FormGroup; 
  isSuccess = false;
  constructor(
    private formBuilder: FormBuilder,
    private formEmailService: FormEmailService,
    private _convertkit: ConvertKitService,
    
  ) { }

  ngOnInit() {
    this.cornerStoneClubForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      email: [
        '',
        [Validators.required, Validators.pattern(EMAIL.EMAIL_REGEX)]
      ],
      isAccredited: '1'
    });
  }

  get controls() {
    return this.cornerStoneClubForm.controls;
  }

  scrollToSection() {
    var element = document.getElementById("join-corner-stone-club-section");
    element.scrollIntoView();
    element.scrollIntoView(false);
    element.scrollIntoView({block: "start"});
    element.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
  }

  submit() {
    if (this.cornerStoneClubForm.invalid) {
      return;
    }
    this.setCornerstoneClub(this.cornerStoneClubForm);
  }

  setCornerstoneClub(props) {
    const info = Object.assign({}, props.value);
    info.isAccredited = !!parseInt(info.isAccredited, 10);
    this.formEmailService.setCornerstoneClub(info).subscribe(res => {
      this.isSuccess = true;
      this.cornerStoneClubForm.reset();
      this.controls.isAccredited.setValue('1');
    });
  }


}
