import { Component, OnInit, Input, ViewChild } from '@angular/core';
import {
  FinancialCapitalDetails,
  FinancialCapitalGrowth,
  FinancialExpense,
  FinancialOverview,
  FinancialIncome
} from '@shared/api/property-details/property-details.model';

import { Chart } from 'chart.js';

import { numberAbbreviator } from '@shared/utilities/numberFormatter';
import { PropertyDetailsService } from '@shared/api';
import { ActivatedRoute } from '@angular/router';
import { MessageService } from '@pages/components/message/message.service';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'tab-financial-details',
  templateUrl: './tab-financial-details.component.html',
  styleUrls: ['./tab-financial-details.component.scss']
})
export class TabFinancialDetailsComponent implements OnInit {
  propertyId;

  propertyValue;
  capitalDetails;
  capitalGrowth;
  capitalList;
  capitalGrowthYears;
  expenseDetails;
  totalExpense;
  totalProjectedExpense: number = 0;
  totalActualExpense: number = 0;
  overviewDetails;
  incomeDetails;

  _details;
  _showActuals: boolean = false;
  showBanner: boolean = false;
  @Input()
  set details(data) {
    if (!data) {
      return;
    }
    this._details = data;
    this._showActuals = data.showActuals;
    this.showNotification(true);
  }

  @Input()
  showProjected: boolean;

  dataFilter;
  selectedFilter;
  @Input()
  set setFilters(data: any) {
    if (!data || !data.length) {
      return;
    }
    this.dataFilter = data;
    const timeFrame = data.find(i => i.name === 'timeFrame');
    this.selectedFilter = timeFrame.data.find(i => i.key === 'Last12Months');
  }

  isCollapsed: boolean = false;

  constructor(
    private activeRoute: ActivatedRoute,
    private _propertyDetailsService: PropertyDetailsService,
    private _notification: MessageService,
    private datePipe: DatePipe
  ) {}

  ngOnInit() {
    this.propertyId = this.activeRoute.snapshot.params.id || 0;
    if (!this.propertyId) {
      return;
    }
    const filter = {
      TimeFrame: 'Last12Months',
      Months: null
    };
    this.getInfo(filter);
    this.getFinancialCapitalGrowth();
    this.getFinancialCapitalDetails();
  }

  getInfo(filter) {
    this.getFincancialOverview(filter);
    this.getFinancialIncome(filter);
    this.getFinancialExpense(filter);
  }

  showNotification(showThis: boolean = false) {
    if (
      !this._showActuals ||
      !this._details ||
      !this._details.annualizedReturn ||
      !showThis
    ) {
      this.showBanner = false;
      return;
    }
    this.showBanner = true;
  }

  getFincancialOverview(filter) {
    this._propertyDetailsService
      .getFinancialOverview(this.propertyId, filter)
      .subscribe(data => {
        this.overviewDetails = data;
      });
  }

  getFinancialIncome(filter) {
    this._propertyDetailsService
      .getFinancialIncome(this.propertyId, filter)
      .subscribe(data => {
        this.incomeDetails = data;
      });
  }

  getFinancialExpense(filter) {
    this._propertyDetailsService
      .getFinancialExpense(this.propertyId, filter)
      .subscribe(res => {
        this.expenseDetails = res.data;
        this.totalProjectedExpense = (res && res.projectedTotalExpense) || 0;
        this.totalActualExpense = (res && res.actualTotalExpense) || 0;
      });
  }

  getFinancialCapitalGrowth() {
    this._propertyDetailsService
      .getFinancialCapitalGrowth(this.propertyId)
      .subscribe(data => {
        if (!data || !data.length) {
          return;
        }
        this.capitalGrowthYears = data.length;
        this.capitalList = data;
        this.capitalGrowth = data[0] || {};
      });
  }

  getFinancialCapitalDetails() {
    this._propertyDetailsService
      .getFinancialCapitalDetails(this.propertyId)
      .subscribe(data => {
        this.capitalDetails = data;
        this.propertyValue =
          (data && data.length && data[0].propertyValue) || 0;
      });
  }

  setSelectedFilter(evt) {
    const filterType = this.dataFilter.find(i => i.name === evt.type);
    this.selectedFilter = filterType.data.find(i => i.name === evt.name);
    const filter = {
      Months: evt.type === 'months' ? this.selectedFilter.key : null,
      TimeFrame: evt.type === 'timeFrame' ? this.selectedFilter.key : null
    };
    this.getInfo(filter);
    const show = this.selectedFilter.key === 'Last12Months' || false;
    this.showNotification(show);
  }
}
