import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplateMComponent } from '@modules/main-app/components/templates/template-m/template-m.component';
import { TemplateNComponent } from '@modules/main-app/components/templates/template-n/template-n.component';
@Component({
  selector: 'app-stayv2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class Stayv2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Staying At The Property`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'Stay At The Property',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/staying-at-the-property/Property_lg.jpg',
              title: 'STAYING AT THE PROPERTY',
              description:
                'As a part owner you are welcome to book and stay at any property that you own parts in. We are able to extend a small discount to you, and you will get first access to book the property. ',
              button: {}
            }
          },
          {
            keyTerm: 'Stay At The Property Content',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/Box-bg-2-col-copy.png',
              numRows: 2,
              boxes: [
                {
                  title: 'Discounted Rate',
                  image: '/assets/icons/save-icon.svg',
                  description:
                    'As a direct booking via Key Lobby (the management company) you will be entitled to a discounted rate at the property. <br/><br/>This usually equates to an approximate 10% discount, and is equal to the booking fees that would usually be incurred.<br/><br/><small>Note - The discount applied does not affect other investors as the amount is based on the advertising/booking fee savings made from a direct booking.</small>'
                },
                {
                  title: 'First Access',
                  image: '/assets/icons/open-icon.svg',
                  description:
                    'You will have first access to newly opened calendar months for the properties you own parts in. This means that you can book a property for a stay before it is opened up to the general public. <br/><br/>We allow bookings up to 9 months in advance for public bookings, and 12 months in advance for part owners.'
                }
              ]
            }
          },
          {
            keyTerm: 'Already a part owner',
            component: TemplateNComponent,
            data: {
              image: '/assets/staying-at-the-property/Image@2x.png',
              title: 'Already a part owner?',
              description:
                'Simply log into your dashboard, choose your property and make a booking',
              button: {
                link: '/login',
                text: 'Login Now'
              }
            }
          }
        ];

      case TENANTS.TPC:
        return [];
      default:
        break;
    }
  }
}
