import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

import { TABS } from '@shared/constants/property-details';
import { VideoPlayerService } from '@shared/services';
import { ResponseContentType, Http, RequestOptions } from '@angular/http';
import { FileSaverService } from 'ngx-filesaver';

import { environment } from '@env/environment';
import { MessageService } from '@pages/components/message/message.service';

@Component({
  selector: 'tab-summary',
  templateUrl: './tab-summary.component.html',
  styleUrls: ['./tab-summary.component.scss']
})
export class TabSummaryComponent implements OnInit {
  summaryData;
  summaryDataArr;
  summaryPrivatDocs;
  privateDocsPath;
  financialSummary;
  images: string = '/assets/img/walkthrough.jpg';
  public baseUrl = environment.baseUrl;

  @Input()
  set Images(images: Array<any>) {
    if (images && images.length) {
      this.images = `${environment.baseUrl}${images[0].filePath}`;
    }
  }

  @Input()
  set summaryDetails(data) {
    this.summaryData = data || {};
    this.summaryDataArr = data ? [data] : [];
  }

  @Input()
  set privateDocs(data) {
    if (data) {
      this.privateDocsPath = `${environment.baseUrl}${data.filePath}`;
      this.summaryPrivatDocs = data;
    }
  }

  @Input()
  set financial(data) {
    this.financialSummary = data;
  }

  @Input()
  propertyValue;

  @Input()
  propertyPart;

  @Input()
  callback: Function;

  @Input()
  showActuals: boolean;

  @Output()
  changeActiveTabs = new EventEmitter();

  tabsIndex;

  constructor(
    private http: Http,
    private _FileSaverService: FileSaverService,
    private _videoPlayer: VideoPlayerService,
    private _notification: MessageService
  ) {
    this.tabsIndex = TABS;
  }

  ngOnInit() {}

  openOtherTabs(idx) {
    this.changeActiveTabs.emit(idx);
  }

  downloadDocs(status: number) {
    if (status === 3) {
      this._notification.create('warning', `The PPM is not yet available`, {
        Position: 'top-right',
        Style: 'flip',
        Duration: 3000
      });
      return;
    }
    const options = new RequestOptions({
      responseType: ResponseContentType.Blob
    });

    this.http.get(this.privateDocsPath, options).subscribe(res => {
      this._FileSaverService.save(
        (<any>res)._body,
        this.summaryPrivatDocs.fileName
      );
    });
  }

  playVideo(videoLink, title) {
    this._videoPlayer.toggle();
    this._videoPlayer.setVideo(videoLink);
    this._videoPlayer.setTitle(title);
  }
}
