import {
  Component,
  OnInit,
  Input,
  HostListener,
  ChangeDetectorRef
} from '@angular/core';
import { PropertyLocation } from '@shared/api/property-details/property-details.model';

@Component({
  selector: 'tab-property-location',
  templateUrl: './tab-property-location.component.html',
  styleUrls: ['./tab-property-location.component.scss']
})
export class TabPropertyLocationComponent implements OnInit {
  propertyLocation: PropertyLocation;
  slideHeight = `300px`;
  googleMapData = [];
  showMap = true;
  showNeighborhoodPhotos = true;
  mapHeight = `250px;`;
  mapControlPosition = `LEFT_TOP`;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this.updateElements();
      this.changeDetectorRef.detectChanges();
    }, 500);
  }

  @Input()
  set PropertyLocationInfo(info) {
    this.propertyLocation = info;
    this.googleMapData = [];
    const marker: { [key: string]: any } = {};

    marker.locationLatitude = this.propertyLocation.locationLatitude;
    marker.locationLongitude = this.propertyLocation.locationLongitude;
    marker.propertyID = this.propertyLocation.propertyID;
    marker.propertyAddress = this.propertyLocation.propertyAddress;
    marker.propertyName = this.propertyLocation.propertyName;

    this.googleMapData.push(marker);
  }

  constructor(private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.updateElements();
  }

  updateElements() {
    const checkMobile = window.innerWidth < 768;
    this.slideHeight = checkMobile ? `250px` : `300px`;
    this.mapHeight = checkMobile ? `250px` : `300px`;
    this.mapControlPosition = checkMobile ? `RIGHT_BOTTOM` : `LEFT_TOP`;

    if (checkMobile) {
      this.showNeighborhoodPhotos = false;
      this.showMap = true;
    } else {
      this.showNeighborhoodPhotos = true;
      this.showMap = true;
    }
  }
}
