import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplateKComponent } from '@modules/main-app/components/templates/template-k/template-k.component';
import { TemplateSComponent } from '@modules/main-app/components/templates/template-s/template-s.component';
@Component({
  selector: 'app-how-does-it-workv2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class HowDoesItWorkv2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this.title.setTitle(`The Property Crowd - How Does It Work`);
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'How Does It Work Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/img/how-does-it-work/works_lg.jpg',
              title: 'HOW DOES IT WORK ?',
              description:
                'Becoming a part owner of an income producing property has never been easier. Well show you how.',
              button: {
                text: 'Watch Our Video',
                link: '',
                icon: '/assets/img/home/Play.svg'
              }
            }
          },
          {
            keyTerm: 'How Does It Work Content',
            component: TemplateKComponent,
            data: {
              sections: [
                {
                  title: 'We buy suitable properties in the <b>Caribbean</b>.',
                  content:
                    'Our team of experts spends time researching the appropriate Caribbean Markets to determine which properties will generate the most income from short term rentals.<br/><br/>We use these findings to guide our property selection and negotiate the best purchase price possible.',
                  image: 'assets/img/how-does-it-work/1.png',
                  button: {}
                },
                {
                  title: 'Keylobby prepares the property for renters.',
                  content:
                    'We use a Caribbean-based property management company called KeyLobby to get the property ready for renters. They take care of all the necessary renovations and prepare the property to be listed as a short term vacation rental.<br/><br/>The team at KeyLobby posts the listing on Airbnb, VRBO, and other popular sites to maximize exposure and ensure a high rate and occupancy for the property.',
                  image: 'assets/img/how-does-it-work/2.png',
                  button: {
                    text: 'Visit KeyLobby',
                    link: 'http://keylobby.com',
                    openNewTab: true
                  }
                },
                {
                  title: 'The property is listed on our <b>platform</b>.',
                  content:
                    'Once a property is ready to rent, we list it on our marketplace. The platform is designed to provide you with every detail about the properties we offer. Read about the region, the suburb, and past property performance. <br/><br/>We provide you with detailed income, expense, and yield projections for each property. Once a listing is up and running, these projections will be updated with actual figures.',
                  image: 'assets/img/how-does-it-work/3.png',
                  button: {
                    text: 'Read More',
                    link: '/app/marketplace',
                    openNewTab: false
                  }
                },
                {
                  title: 'You receive <b>income</b> every month.',
                  content:
                    'Each month we will compile financial accounts for the property to assess the income / expenses.<br/><br/>You will receive your share of the income (after expenses have been deducted) directly credited into your digital wallet within 15 days. You can choose to withdraw these funds or re-invest them into other properties.',
                  image: 'assets/img/how-does-it-work/4.png',
                  button: {
                    text: 'Read More',
                    link: '/app/returns',
                    openNewTab: false
                  }
                },
                {
                  title: '<b>Stay</b> at the property.',
                  content:
                    'As a part owner, you can book a stay at the property at a discounted rate. This rate eliminates the usual booking fees that would be incurred on other platforms, giving owners a discount of approximately 10% off the standard rate.<br/><br/>Eligible owners will also receive exclusive access to schedule their stay before dates are released to the open market.',
                  image: 'assets/img/how-does-it-work/5.png',
                  button: {}
                },
                {
                  title: '<b>Buy</b> and <b>Sell</b> your parts.',
                  content:
                    'You can buy parts anytime from other investors (subject to availability) or choose to list your parts for sale on the platform to other investors at a price that you feel comfortable with. You must hold your parts for a minimum of 6 months before you are able to list them for re-sale.',
                  image: 'assets/img/how-does-it-work/6.png',
                  button: {}
                }
              ]
            }
          }
        ];
      case TENANTS.TPC:
        return [
          {
            keyTerm: 'How Does It Work Header',
            component: TemplateSComponent,
            data: {
              backgroundURL:
                '/assets/img/tpc/how_does_it_works/business-business-women-collaborate-1569076@2x.jpg',
              title: 'How Investing in TPC Works',
              image: '/assets/img/tpc/how_does_it_works/Group29@2x.png'
            }
          },
          {
            keyTerm: 'How Does It Work Content',
            component: TemplateKComponent,
            data: {
              title: 'How investing in TPC works',
              sections: [
                {
                  title: 'Open an account and get verified. ',
                  content:
                    'You can sign up to invest in PropertyShares by opening an account.<br/><br/>For your security, this process requires you to ""get verified"". We\'ll need your official identification and ask you some personal questions to confirm your identity.',
                  image: 'assets/img/tpc/how_does_it_works/Group19@2x.jpg',
                  button: {
                    text: 'Open an account now',
                    link: '/app/tpc-home',
                    openNewTab: false
                  }
                },
                {
                  title: 'Fund your account',
                  content:
                    'You can easily transfer money into your wallet from your NZ bank account. <br/><br/>It needs to be from a cheque or transaction account. Unfortunately, we cannot process transactions from savings accounts.<br/><br/>We hold your money in an ASB Trust Account to ensure your funds are separated from the rest of our operations.<br/><br/>You can then withdraw your money at any time.',
                  image: 'assets/img/tpc/how_does_it_works/Group20@2x.jpg',
                  button: {}
                },
                {
                  title: 'Review our quality properties.',
                  content:
                    'The Property Crowd properties have passed our evaluation criteria which assesses build quality, compliance, location and scope for return.<br/><br/>To help protect your investment all of our properties are managed by a specialist independent Property Management Company.<br/><br/>They are maintained to a high standard and let to long-term tenants.',
                  image: 'assets/img/tpc/how_does_it_works/Group21@2x.jpg',
                  button: {
                    text: 'Review our property',
                    link: '/app/tpc-home',
                    openNewTab: false
                  }
                },
                {
                  title: 'Invest',
                  content:
                    'Once you have been verified and have funded your account, you can start purchasing PropertyShares. <br/><br/>All our properties will clearly list a detailed breakdown of the financials involved. Our aim is to be as transparent as possible.',
                  image: 'assets/img/tpc/how_does_it_works/Group25@2x.jpg',
                  button: {}
                },
                {
                  title: 'Receive monthly dividends',
                  content:
                    "Once fully funded our Property Management Companies secure the tenancies and manage the properties. You'll receive monthly dividends based on your PropertyShare ownership. <br/><br/>Rent is collected monthly and is shared proportionately between the investors who have shares in that property's company. The rental income is paid net any management fees and maintenance costs one month in arrears. <br/><br/>The money is paid directly into your wallet where you can either withdraw your monthly income, or reinvest it to build your portfolio.",
                  image: 'assets/img/tpc/how_does_it_works/Group26@2x.jpg',
                  button: {}
                },
                {
                  title: 'Track your investment',
                  content:
                    'You can view the progress of your investment via your portfolio screen. <br/><br/>Regular updates will be made available such as rental income, property inspections, market and neighbourhood valuations, and more.',
                  image: 'assets/img/tpc/how_does_it_works/Group27@2x.jpg',
                  button: {}
                },
                {
                  title: 'Sell your shares',
                  content:
                    "When you are ready, you can sell your PropertyShares by listing them for sale on our Resale Market. <br/><br/>Choose your selling price guided by recent valuations and sales.<br/><br/>Once a buyer agrees to purchase your Property Shares you'll receive the full value of your PropertyShares including any capital gains.  There is no fee for selling PropertyShares.  You get to keep the full value when you sell.",
                  image: 'assets/img/tpc/how_does_it_works/Group28@2x.jpg',
                  button: {}
                }
              ]
            }
          }
        ];
    }
  }
}
