import { Component, OnInit } from '@angular/core';
import { PropertyDetailsService } from '@shared/api';
import { environment } from '@env/environment';
import { RequestOptions, ResponseContentType, Http } from '@angular/http';
import { FileSaverService } from 'ngx-filesaver';

@Component({
  selector: 'app-offering-information',
  templateUrl: './offering-information.component.html',
  styleUrls: ['./offering-information.component.scss']
})
export class OfferingInformationComponent implements OnInit {
  activeTabs = 0;
  navbarShow = 'none';
  navMobileTitle = 'Regulation D Offerings';
  regulatoryOfferingsContent = 'inline';
  regulatoryFillingsContent = 'none';
  taxForms = 'none';

  constructor() {}

  setTabIndex(event) {
    this.activeTabs = event;
  }

  openNavbar() {
    if (this.navbarShow === 'block') {
      this.navbarShow = 'none';
    } else {
      this.navbarShow = 'block';
    }
  }

  openRegulatoryDOfferings() {
    this.navMobileTitle = 'Regulation D Offerings';
    this.regulatoryOfferingsContent = 'inline';
    this.regulatoryFillingsContent = 'none';
    this.taxForms = 'none';
    this.navbarShow = 'none';
  }

  openRegulatoryFillingsContent() {
    this.navMobileTitle = 'Regulatory Fillings';
    this.regulatoryOfferingsContent = 'none';
    this.regulatoryFillingsContent = 'inline';
    this.taxForms = 'none';
    this.navbarShow = 'none';
  }

  openTaxForms() {
    this.navMobileTitle = 'Tax Forms';
    this.regulatoryOfferingsContent = 'none';
    this.regulatoryFillingsContent = 'none';
    this.taxForms = 'inline';
    this.navbarShow = 'none';
  }

  ngOnInit() {}
}
