import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplateMComponent } from '@modules/main-app/components/templates/template-m/template-m.component';
@Component({
  selector: 'app-managev2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class Managev2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Project Management`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'Project Management Page Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/how-it-is-managed/Managed_lg.jpg',
              title: 'HOW IS THE PROPERTY MANAGED ?',
              description:
                ' Key Lobby handles the set-up and management for every property in the Partbnb portfolio. Read on to find out more about how this local property management company cares for your investment.',
              button: {}
            }
          },
          {
            keyTerm: 'Fees Page, Platform Fees',
            component: TemplateMComponent,
            data: {
              boxBackground: '/assets/Box-bg-2-col-copy.png',
              title: 'Overview',
              subDescription:
                'With almost five years experience and a growing network of properties, Key Lobby is a true top performer in the property management industry. Recently, theyve become the sole management company for Partbnb, focusing on the Caribbean market. <br/> <br/>The Key Lobby team prides themselves on providing a fantastic service for not only the property owners, but their future guests. Their hard work and dedication shine through in every glowing client testimonial on their website. If you have any questions about Key Lobby or the process we follow to prepare your property for the market, feel free to contact our team.<br/><br/>Key Lobby have great testimonials from their clients which made our decision easy, check out their testimonial section on their website or talk to our team if you have any questions.',
              numRows: 2,
              boxes: [
                {
                  title: 'Property Setup',
                  image: '/assets/icons/occupancy-icon.svg',
                  description:
                    'It is essential that the property is set up to be suitable for short term accommodation. KeyLobby provides a full-service approach, from choosing the right locks and monitoring equipment to ordering the best furniture and amenities. They also ensure that each item is placed in the right areas throughout the house.'
                },
                {
                  title: 'Guest Inquiries',
                  image: '/assets/icons/listing-icon.svg',
                  description:
                    'A management company that can turn enquiries into confirmed bookings is crucial. KeyLobby excels at converting even the most unlikely of general questions into complete bookings. Managing bookings is one of the most time-consuming tasks of running a short term rental, and KeyLobby takes away this hassle for owners.'
                },
                {
                  title: 'During the Stay',
                  image: '/assets/icons/checking-icon.svg',
                  description:
                    'KeyLobby is available on a 24/7 basis to respond quickly to any issues via email or phone. Property visits, cleaning, and maintenance are conducted regularly. Should problems arise, a team of experts is on-hand to tackle emergency requests.'
                },
                {
                  title: 'Listing Experts',
                  image: '/assets/icons/listing-icon.svg',
                  description:
                    'Having the property setup correctly online is essential when it comes to attracting renters. The KeyLobby team ensures that your property details are accurate and engaging. Properties need to be accurately and continuously priced online throughout different seasons of the year to ensure that the property is generating great occupancy and returns for its owners. <br/><br/>KeyLobby pricing pros utilize the latest in technology from AirDNA to accurately price property listings.'
                }
              ]
            }
          },
          {
            keyTerm: 'Visit Keylobby Website',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/how-it-is-managed/Bg_sm.jpg',
              title: 'Visit the Keylobby Website',
              button: {
                link: 'https://www.keylobby.com',
                text: 'Click Here to Visit'
              }
            }
          }
        ];

      case TENANTS.TPC:
        return [];
      default:
        break;
    }
  }
}
