import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { TENANTS, TENANTS_INFO } from '@shared/constants/tenants.ts';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';
import { ComponentLoaderService } from '@modules/main-app/services/component-loader/component-loader.service';
import { TemplateFComponent } from '@modules/main-app/components/templates/template-f/template-f.component';
import { TemplatePComponent } from '@modules/main-app/components/templates/template-p/template-p.component';
@Component({
  selector: 'app-comparisonv2',
  template: `
    <ng-template renderPoint></ng-template>
  `
})
export class Comparisonv2Component implements OnInit {
  currentTenant;
  components;

  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 250);
  }

  constructor(
    private _tenantCheckerService: TenantCheckerService,
    private _viewportService: ViewportService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _componentLoaderService: ComponentLoaderService
  ) {}

  ngOnInit() {
    this._urlQueryStringService.checkURLQueryString();
    this._viewportService.checkViewSizes();
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.title.setTitle(
      `${
        TENANTS_INFO.filter(tenant => tenant.id === this.currentTenant)[0].name
      } - Comparison Options`
    );
    this._componentLoaderService.setComponents(this.getComponents());
    this._componentLoaderService.setViewChild(this.renderPointRef);
    this._componentLoaderService.loadComponent();
  }

  getComponents() {
    switch (this.currentTenant) {
      case TENANTS.PARTBNB:
        return [
          {
            keyTerm: 'Comparison Option Header',
            component: TemplateFComponent,
            data: {
              backgroundURL: '/assets/who-does-it-suit/Suit_lg.jpg',
              title: 'COMPARISON',
              description:
                'See how owning a Partbnb is much easier, lower risk and could save / make you more money than buying a property yourself outright.',
              button: {}
            }
          },
          {
            keyTerm: 'Who Does It Suit Content',
            component: TemplatePComponent,
            data: {
              table: {
                header: {
                  ourHead: 'PARTBNB',
                  doItYourselfHead: 'DO IT YOURSELF'
                },
                body: [
                  {
                    option: 'Minimum Amount',
                    ourBody: {
                      value: '$50'
                    },
                    doItYourselfBody: {
                      value: '$200k+'
                    }
                  },
                  {
                    option: 'Find a Property Easily',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'We have done all the work - our experienced team purchases the properties at great value, and ensures they are perfectly suited for the short term rental market.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle'
                    }
                  },
                  {
                    option: 'Set Up Done',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'Our expert team sets up every property and ensures it is furnished to get the best occupancy and revenue.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle'
                    }
                  },
                  {
                    option: 'Buy Easy',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description: 'Takes just minutes!'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle',
                      description:
                        'Process can take 3-6 months on some islands.'
                    }
                  },
                  {
                    option: 'Sell Easy',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'You can list your shared up for sale on the platform.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle',
                      description:
                        'Most properties take 6 months to sell on islands.'
                    }
                  },
                  {
                    option: 'No Legal Cost',
                    ourBody: {
                      icon: 'fa-check-circle'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle',
                      description: 'Legal cost often exceed $3k for a purchase.'
                    }
                  },
                  {
                    option: 'Diversification',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'The ability to purchase multiple properties on the platform allows you to diversify your invesment and reduce the risk of something catestrophic occuring to one property.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle',
                      description:
                        '100% investment in one property is higher risk - if something occurs to that one property.'
                    }
                  },
                  {
                    option: 'No Home Loan',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'There is no lending / loans to repay on any Partbnb properties.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-question-circle',
                      description:
                        'In some cases you may require lending to purchase the property. This can be complicated to obtain in the Caribbean.'
                    }
                  },
                  {
                    option: 'No Maintenance Worries',
                    ourBody: {
                      icon: 'fa-check-circle'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle'
                    }
                  },
                  {
                    option: 'No Property Management Worries',
                    ourBody: {
                      icon: 'fa-check-circle'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle'
                    }
                  },
                  {
                    option: 'Easy Accounting',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'Simple Schedule K-1Income statements for your tax filing.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle',
                      description:
                        'You must track income / expenses and then work with an accountant to prepare returns.'
                    }
                  },
                  {
                    option: 'Higher Yields Rental Return',
                    ourBody: {
                      icon: 'fa-check-circle',
                      description:
                        'Our expert property managers use our experience and data to maximize their revenue.'
                    },
                    doItYourselfBody: {
                      icon: 'fa-times-circle'
                    }
                  }
                ]
              }
            }
          }
        ];

      case TENANTS.TPC:
        return [];
      default:
        break;
    }
  }
}
