import { Component, OnInit, Input } from '@angular/core';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';

@Component({
  selector: 'template-o',
  templateUrl: './template-o.component.html',
  styleUrls: ['./template-o.component.scss']
})
export class TemplateOComponent implements OnInit {
  currentTenant;
  baseClassName;
  image;
  isDesktop;
  isTablet;
  isMobile;

  @Input() resources: any;

  constructor(
    private _viewportService: ViewportService,
    private _tenantCheckerService: TenantCheckerService
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.baseClassName = this._viewportService.sizeClassName();
      this.isDesktop = this._viewportService.isDesktop();
      this.isTablet = this._viewportService.isTablet();
      this.isMobile = this._viewportService.isMobile();
      this.image = this.getImage(this.resources.image);
    });
  }

  ngOnInit() {
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.baseClassName = this._viewportService.sizeClassName();
    this.isDesktop = this._viewportService.isDesktop();
    this.isTablet = this._viewportService.isTablet();
    this.isMobile = this._viewportService.isMobile();
    this.image = this.getImage(this.resources.image);
  }

  getImage(image) {
    if (this.isDesktop) {
      return image.desktop;
    } else if (this.isTablet) {
      return image.tablet;
    } else {
      return image.mobile;
    }
  }
}
