import {
  Component,
  OnInit,
  Output,
  AfterViewInit,
  EventEmitter,
  HostListener,
  ViewChild,
  ChangeDetectorRef
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '@env/environment';

import { TABS } from '@shared/constants/property-details';

import { PropertyService, UsersService } from '@shared/api';
import { Property } from '@shared/api/property/property.model';
import {
  PropertySummary,
  FinancialOverview,
  FinancialIncome,
  FinancialCapitalDetails,
  FinancialCapitalGrowth,
  PropertyLocation,
  FloorPlanLayout,
  Timeline,
  Video,
  FinancialExpense,
  FinancialSummary
} from '@shared/api/property-details/property-details.model';
import { PhotoGalleryService } from '@shared/services/photo-gallery/photo-gallery.service';
import { PhotoCarouselService } from '@shared/services/photo-carousel/photo-carousel.service';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import {
  PropertyImages,
  PropertyAssetTypes
} from '@shared/api/property-details/property-details.model';
import { NgbCarousel } from '@ng-bootstrap/ng-bootstrap';
import { PROPERTY_STATUS } from '@shared/constants/property-details';
import { Title } from '@angular/platform-browser';
import { UrlQueryStringService } from '@shared/services/url-query-string/url-query-string.service';
import { BsModalService } from 'ngx-bootstrap';
import {
  ConfirmationComponent,
  ConfirmationProps
} from '@shared/components/modals/confirmation';
import { CurrentUserService } from '@shared/services';
import { VERIFICATION_STATUS } from '@shared/constants/verification-status';
import { InvestorVerificationComponent } from '@shared/components/modals/investor-verification/investor-verification.component';
import { OrderPropertyModalComponent } from '@shared/components/modals/order-property-modal/order-property-modal.component';
import { ModalService } from '@shared/services/modal/modal.service';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { HEIGHT } from '@shared/constants/property-carousel.ts';

@Component({
  selector: 'app-property-details',
  templateUrl: './property-details.component.html',
  styleUrls: ['./property-details.component.scss']
})
export class PropertyDetailsComponent implements OnInit, AfterViewInit {
  closeBanner: boolean = false;
  activeTabs;

  images: string[];
  propertyId: number;
  propertyDetails: Property;
  summaryDetails: PropertySummary;
  summaryPrivateDocs: PropertyImages;
  testInput: 'string';
  propertyImages: PropertyImages;
  propertyAssetTypes: PropertyAssetTypes[];
  baseUrl = environment.baseUrl;
  propertyAddress;
  propertyLocation: PropertyLocation;
  floorPlanLayout: FloorPlanLayout;
  video: Video;
  isPreOrderNotAllow: boolean = false;
  showActuals: boolean;
  showProjected: boolean;

  // Financial Tabs
  financialOverview: FinancialOverview;
  financiaIncome: FinancialIncome;
  financialExpense: FinancialExpense;
  financialCapitalDetails: FinancialCapitalDetails[];
  financialCapitalGrowth: FinancialCapitalGrowth[];
  timelines: Timeline[];
  financialSummary: FinancialSummary;
  financialFilters;

  isLogin: boolean = false;
  hideBanner: boolean = true;
  customOptions: OwlOptions = {
    navSpeed: 700,
    center: true,
    nav: false,
    mouseDrag: false,
    dots: false,
    loop: true,
    autoWidth: true,
    margin: 3,
    lazyLoadEager: 3
  };
  loadImgCounter = 0;
  propertyImageLength = 0;
  carouselHeight = HEIGHT.DESKTOP;

  @Output()
  openImageGallery = new EventEmitter();

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this._viewportService.checkViewSizes();
    }, 500);
  }

  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    private bsModalService: BsModalService,
    private _propertyService: PropertyService,
    private _photoGalleryService: PhotoGalleryService,
    private _propertyDetailsService: PropertyDetailsService,
    private changeDetectorRef: ChangeDetectorRef,
    private _photoCarouselService: PhotoCarouselService,
    private title: Title,
    private _urlQueryStringService: UrlQueryStringService,
    private _users: UsersService,
    private _currentUser: CurrentUserService,
    private _modalService: ModalService,
    private _viewportService: ViewportService
  ) {
    this._viewportService.updatingView.subscribe(() => {
      if (this._viewportService.isDesktop()) {
        this.carouselHeight = HEIGHT.DESKTOP;
      } else if (this._viewportService.isTablet()) {
        this.carouselHeight = HEIGHT.TABLET;
      } else if (this._viewportService.isMobile()) {
        this.carouselHeight = HEIGHT.MOBILE;
      }
      this.loadImgCounter = 0;
      if (this.propertyImages) {
        this.generateCarouselWidth(this.propertyImages);
        this.changeDetectorRef.detectChanges();
      }
    });
  }

  ngOnInit() {
    this.title.setTitle(`Partbnb - Property Details`);
    if (!this.activeRoute.snapshot.params.hasOwnProperty('id')) {
      this.router.navigate(['/app/properties']);
    } else {
      const currentUser = JSON.parse(localStorage.getItem('currentUser'));
      this.isLogin = (currentUser && currentUser.isLogin) || false;
      this._urlQueryStringService.checkURLQueryString();
      this.activeTabs = TABS.SUMMARY;
      this.propertyId = this.activeRoute.snapshot.params.id || 0;
      this.getDetails(this.propertyId);
      this.getPropertyAssetTypes();
      this.getFloorPlanLayout(this.propertyId);
      this.getTimelines(this.propertyId);
      this.getVideo(this.propertyId);
      this.getSummaryDetails(this.propertyId);

      // this.getFinancialCapitalGrowth();
      // this.getFinancialCapitalDetails();
      this.getPropertyLocation();
      this.getFinancialSummary();
    }
  }

  ngAfterViewInit() {
    this._viewportService.checkViewSizes();
  }

  generateCarouselWidth(images) {
    images.map((image, i) => {
      const img = new Image();
      img.src = this.baseUrl + image.filePath;
      const self = this;
      img.onload = function() {
        self.propertyImages[i].width = Math.round(
          self.carouselHeight * (img.width / img.height)
        );

        self.loadImgCounter++;
        return;
      };
    });
  }

  changeActiveTabs(idx) {
    this.activeTabs = idx;
  }

  setTabIndex(event) {
    this.activeTabs = event.index;
  }

  getDetails(propertyId) {
    this._propertyService.getPropertyDetails(propertyId).subscribe(
      res => {
        if (res.data.propertyListingTypeId === PROPERTY_STATUS.PLACEHOLDER) {
          this.router.navigate(['/app/properties']);
        }
        this.propertyDetails = res && res.data;
        this.financialFilters = res && res.options;
        this.showActuals = this.propertyDetails.showActuals || false;
        this.showProjected = this.propertyDetails.showProjected || false;
        this.propertyAddress = this.propertyDetails.propertyAddress;
        this.hideBanner =
          this.propertyDetails.propertyStatusId === PROPERTY_STATUS.LIVE;
        if (res.data.propertyStatusId !== PROPERTY_STATUS.PRE_ORDER) {
          this.isPreOrderNotAllow = true;
          return;
        }
        this.checkProperty(propertyId);
      },
      () => {
        this.router.navigate(['/app/properties']);
      }
    );
  }

  checkProperty(propertyId: number) {
    this.isPreOrderNotAllow = false;
    const currentUser = JSON.parse(localStorage.getItem('currentUser')) || {};
    if (propertyId && currentUser.isLogin) {
      this._users
        .checkUserForPreOrder(currentUser.username, propertyId)
        .subscribe(res => {
          this.isPreOrderNotAllow = !(res && res.canPreOrder);
        });
    }
  }

  getSummaryDetails(propertyId) {
    this._propertyDetailsService
      .getPropertySummary(propertyId)
      .subscribe(data => {
        const overviewArr = data.overview && data.overview.split('\\n');
        const locationSummaryArr =
          data.locationSummary && data.locationSummary.split('\\n');
        const businessCaseArr =
          data.businessCase && data.businessCase.split('\\n');

        this.summaryDetails = {
          ...data,
          overviewArr,
          locationSummaryArr,
          businessCaseArr
        };

        this._propertyDetailsService
          .getPropertyImages({ propertyId, propertyAssetTypeId: 5 })
          .subscribe(res => {
            if (res) {
              this.summaryPrivateDocs = res[0];
            }
          });
      });
  }

  getPropertyImages(detailIds) {
    this._propertyDetailsService
      .getPropertyImages(detailIds)
      .subscribe(data => {
        this.propertyImages = data;
        this.propertyImageLength = (<any>this.propertyImages).length;
        this.generateSlideIds(this.propertyImages);
        this.generateCarouselWidth(this.propertyImages);
      });
  }

  getPropertyAssetTypes() {
    this._propertyDetailsService.getPropertyAssetTypes().subscribe(data => {
      this.propertyAssetTypes = data;

      this.getPropertyImages({
        propertyAssetTypeId: this.getPropertyAssetTypeId('Property Photo'),
        propertyId: this.propertyId
      });
    });
  }

  purchaseItem(item) {
    const {
      verificationStatus,
      isAccreditedInvestor
    } = this._currentUser.getUserInfo();
    if (!isAccreditedInvestor) {
      this.openWarningForNonAccredited();
      return;
    }
    if (verificationStatus === VERIFICATION_STATUS.APPROVED) {
      if (item.propertyListingTypeId === PROPERTY_STATUS.PRE_ORDER) {
        this.router.navigate([`/preorder/${item.propertyId}`]);
        return;
      }
      const details = {
        ...item
      };
      this._modalService.modalInstance(OrderPropertyModalComponent, {
        initialState: { details },
        animated: false,
        class: 'centered-modal'
      });
    } else {
      const props: ConfirmationProps = {
        status: 'warning',
        title: 'You need to be verified as an Accredited Investor',
        desc: `We need to verify your status as an Accredited Investor before you can proceed.`,
        isButtonDisabled:
          verificationStatus === VERIFICATION_STATUS.PENDING || false,
        buttonLabel:
          verificationStatus === VERIFICATION_STATUS.PENDING
            ? 'Verification Pending'
            : 'Start Verification'
      };
      const modalRef = this._modalService.modalInstance(ConfirmationComponent, {
        initialState: { props },
        animated: false,
        class: 'confirmation'
      });
      (<ConfirmationComponent>modalRef).onClose.subscribe(result => {
        if (result) {
          this.openInvestorVerify();
        }
      });
    }
  }

  openWarningForNonAccredited() {
    const props: ConfirmationProps = {
      status: 'warning',
      title: 'Sorry, our offer isnt quite ready for Non Accredited Investors',
      desc: `We hope to have this offer available to all investors by
      August 2019 - we will be in touch with you to confirm availability.`,
      buttonLabel: 'Got it'
    };
    this._modalService.modalInstance(ConfirmationComponent, {
      initialState: { props },
      animated: false,
      class: 'confirmation'
    });
  }

  openInvestorVerify() {
    this._modalService.modalInstance(InvestorVerificationComponent, {
      ignoreBackdropClick: true,
      class: 'modal-lg'
    });
  }

  showImageGallery(id = 0) {
    this._photoGalleryService.toggle();
    this._photoGalleryService.setImages(this.propertyImages);
    this._photoGalleryService.setShowThumbnails(true);
    this._photoGalleryService.selectedImage(id);
    this._photoCarouselService.setImageIndex(id);
  }

  concatBaseUrl(url) {
    return this.baseUrl.concat(url);
  }

  getPropertyAssetTypeId(assetTypeName) {
    const selectedAssetType = this.propertyAssetTypes.filter(
      assetType => assetType.propertyAssetTypeName === assetTypeName
    );
    return (
      selectedAssetType.length > 0 && selectedAssetType[0].propertyAssetTypeId
    );
  }

  generateSlideIds(images) {
    images.map((image, index) => {
      image.slideId = index;
    });
  }

  isPropertyPreOrder() {
    return (
      this.propertyDetails.propertyListingTypeId === PROPERTY_STATUS.PRE_ORDER
    );
  }

  // Financial Details

  getFinancialCapitalGrowth() {
    this._propertyDetailsService
      .getFinancialCapitalGrowth(this.propertyId)
      .subscribe(data => {
        this.financialCapitalGrowth = data;
      });
  }

  getFinancialCapitalDetails() {
    this._propertyDetailsService
      .getFinancialCapitalDetails(this.propertyId)
      .subscribe(data => {
        this.financialCapitalDetails = data;
      });
  }

  getPropertyLocation() {
    this._propertyDetailsService
      .getPropertyLocation(this.propertyId)
      .subscribe(data => {
        this.propertyLocation = data;
      });
  }

  getFinancialSummary() {
    this._propertyDetailsService
      .getFinancialSummary(this.propertyId)
      .subscribe(data => {
        this.financialSummary = data;
      });
  }

  getFloorPlanLayout(id) {
    this._propertyDetailsService.getFloorPlanLayout(id).subscribe(data => {
      this.floorPlanLayout = data;
    });
  }

  getTimelines(id) {
    this._propertyDetailsService.getTimelines(id).subscribe(data => {
      this.timelines = data;
    });
  }

  getVideo(id) {
    this._propertyDetailsService.getVideo(id).subscribe(data => {
      this.video = data;
    });
  }

  navigateTo(url) {
    this.router.navigate([`/${url}`], {
      queryParams: {
        returnUrl: this.router.url.toString()
      }
    });
  }

  openMinimumFindingModal(status: boolean) {
    if (status) {
      const props: ConfirmationProps = {
        status: 'success',
        title: 'Minimum Funding Reached',
        desc: `This property has reached the minimum
        funding target needed in order to guarantee that
        Partbnb will proceed with the purchase
        / settlement / renovation of the property.`,
        buttonLabel: 'Okay, got it'
      };
      this._modalService.modalInstance(ConfirmationComponent, {
        initialState: { props },
        animated: false,
        class: 'confirmation'
      });
    }
  }

  propertyIsLive(status) {
    return (
      status === PROPERTY_STATUS.LIVE || status === PROPERTY_STATUS.FUNDING
    );
  }

  propertyIsPreOrder(status) {
    return status === PROPERTY_STATUS.PRE_ORDER;
  }

  propertyHasNoParts(property) {
    const price = (property && property.pricePerPart) || 0;
    return property && !!property.notForResale
      ? 'No Parts Available'
      : `Buy Parts from $${price}`;
  }
}
