import { Injectable, ComponentFactoryResolver, ViewChild } from '@angular/core';
import { RenderPointDirective } from '@modules/main-app/directives/render-point/render-point.directive';

interface ComponentData {
  resources: any;
}

@Injectable({
  providedIn: 'root'
})
export class ComponentLoaderService {
  @ViewChild(RenderPointDirective) renderPointRef: RenderPointDirective;
  components;

  constructor(private componentFactoryResolver: ComponentFactoryResolver) {}

  setViewChild(reference) {
    this.renderPointRef = reference;
  }

  setComponents(components) {
    this.components = components;
  }

  loadComponent() {
    const viewContainerRef = this.renderPointRef.viewContainerRef;
    viewContainerRef.clear();
    this.components.map(component => {
      let componentRef = viewContainerRef.createComponent(
        this.componentFactoryResolver.resolveComponentFactory(
          component.component
        )
      );
      (<ComponentData>componentRef.instance).resources = component.data;
    });
  }
}
