import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { HomeVideoService } from '@shared/services/home-video-service/home-video.service';

import {
  trigger,
  state,
  stagger,
  query,
  style,
  animate,
  transition
} from '@angular/animations';

@Component({
  selector: 'app-how-does-it-work',
  templateUrl: './how-does-it-work.component.html',
  styleUrls: ['./how-does-it-work.component.scss'],
  animations: [
    trigger('openClose', [
      transition(':enter', [animate('200ms ease-in')]),
      transition(':leave', [animate('200ms ease-in')])
    ])
  ]
})
export class HowDoesItWorkComponent implements OnInit {
  constructor(
    private title: Title,
    private homeVideoService: HomeVideoService
  ) {}

  isWatching: boolean = false;

  ngOnInit() {
    this.title.setTitle(`Partbnb - How Does It Work`);
  }

  openVideo() {
    this.homeVideoService.toggle();
  }

  toggleVideo() {
    this.isWatching = !this.isWatching;
    this.openVideo();
    // this.homeVideoService.setScrollHidden(this.isWatching);
  }
}
