import { Injectable } from '@angular/core';
import { environment } from '@env/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import {
  stepCornerstoneClubBuilderUI,
} from './form-email.mappers';
@Injectable({
  providedIn: 'root'
})
export class FormEmailService {

  constructor(private _http: HttpClient) {}
  baseUrl = environment.apiUrl;

  setCornerstoneClub(props) {
    // console.log(`${this.baseUrl}/email/cornerstone-club?firstName=${props.firstName}&lastName=${props.lastName}&email=${props.email}&isAccredited=${props.isAccredited}`)
    return this._http
      .get(`${this.baseUrl}/email/cornerstone-club?firstName=${props.firstName}&lastName=${props.lastName}&email=${props.email}&isAccredited=${props.isAccredited}`);
      // .pipe(map(stepCornerstoneClubBuilderUI));
  }
}
