import { Component, OnInit } from '@angular/core';
import { PropertyDetailsService } from '@shared/api';
import { FileSaverService } from 'ngx-filesaver';
import { Http, ResponseContentType, RequestOptions } from '@angular/http';
import { environment } from '@env/environment';
import { MessageService } from '@pages/components/message/message.service';

@Component({
  selector: 'regulation-d-offerings-palmeto-palace',
  templateUrl: './regulation-d-offerings-palmeto-palace.component.html',
  styleUrls: ['./regulation-d-offerings-palmeto-palace.component.scss']
})
export class RegulationDOfferingsPalmetoPalaceComponent implements OnInit {
  env = environment.baseUrl;
  photoUrl;
  photoName;

  constructor(
    private propertyDetailService: PropertyDetailsService,
    private _FileSaverService: FileSaverService,
    private http: Http,
    private _notification: MessageService
  ) {}

  ngOnInit() {
    this.getPropertyDetails();
  }

  getPropertyDetails() {
    this.propertyDetailService
      .getPropertyImages({ propertyId: 1, propertyAssetTypeId: 5 })
      .subscribe(res => {
        if (res) {
          this.photoUrl = res[0].filePath;
          this.photoName = res[0].fileName;
        }
      });
  }

  downloadPDF(photoName) {
    // this._notification.create('warning', `The PPM still not available`, {
    //   Position: 'top-right',
    //   Style: 'flip',
    //   Duration: 3000
    // });
    const path = `${this.env}${this.photoUrl}`;
    const options = new RequestOptions({
      responseType: ResponseContentType.Blob
    });

    this.http.get(path, options).subscribe(res => {
      this._FileSaverService.save((<any>res)._body, photoName);
    });
  }
}
