import { Component, OnInit, Input } from '@angular/core';
import { ViewportService } from '@shared/services/viewport/viewport.service';
import { TenantCheckerService } from '@shared/services/tenant-checker/tenant-checker.service';
import groupBy from 'app/shared/utilities/groupBy';
import { FilterFaqsPipe } from '@modules/main-app/pipes/filter-faqs/filter-faqs.pipe';

@Component({
  selector: 'template-t',
  templateUrl: './template-t.component.html',
  styleUrls: ['./template-t.component.scss'],
  providers: [FilterFaqsPipe]
})
export class TemplateTComponent implements OnInit {
  currentTenant;
  baseClassName;
  faqs;
  searchKeyword;
  searchResults;
  searchKeys = ['sectionName', 'question', 'answer'];

  @Input() resources: any;
  constructor(
    private _viewportService: ViewportService,
    private _tenantCheckerService: TenantCheckerService,
    private filterFaqPipe: FilterFaqsPipe
  ) {
    this._viewportService.updatingView.subscribe(() => {
      this.baseClassName = this._viewportService.sizeClassName();
    });
  }

  ngOnInit() {
    this.currentTenant = this._tenantCheckerService.currentTenant;
    this.baseClassName = this._viewportService.sizeClassName();
    this.filterSortFAQs();
  }

  filterSortFAQs() {
    const groupFAQsBySectionId = groupBy(this.resources, 'sectionId');
    this.faqs = Object.values(groupFAQsBySectionId);
    this.faqs = this.faqs.sort((faq1, faq2) => {
      return faq1.sectionId - faq2.sectionId;
    });
    this.faqs = this.faqs.map(faq => {
      faq = faq.sort((faq1, faq2) => {
        return faq1.faqOrder - faq2.faqOrder;
      });
      return { faqs: faq, sectionName: faq && faq[0].sectionName };
    });
  }

  searchFAQs() {
    this.searchResults = [];
    const result = this.filterFaqPipe.transform(
      this.resources,
      this.searchKeyword,
      this.searchKeys
    );

    this.searchResults = result;
    this.searchKeyword = null;
  }
}
